import React, { useEffect, useState } from "react";
import axios from "axios";
import Qna from './Qna';
import Modal from '../modal';
const Qnas = props => {
  const [qnas, setqnas] = useState([]);
  const [url, setUrl] = useState(`http://localhost:8080/api/findAllQna`);
  const [loginMember, setLoginMember] = useState({});

  useEffect(() => {
    const getLoginCheck = () => {
      return new Promise((resolve, reject) => {
        axios
          .get("http://localhost:8080/api/loginid")
          .then(res => {
            setLoginMember(res.data);
            resolve();
          })
          .catch(err => {
            reject();
          });
      });
    };

    const fetchData = async () => {
      const result = await axios(url);
      setqnas(result.data);
    };


    fetchData().then(getLoginCheck);
  }, [url]);
  return (
    <>
      <div
        className="panel-group"
        id="accordion"
        role="tablist"
        aria-multiselectable="true"
      >
        {qnas.map((item, idx)=>{
          return <Qna key={idx} accodionKey={idx} qna={item} lm={loginMember}/>;
        })}
      </div>
      <div align="right">
        <button type="button" className="glyphicon glyphicon-pencil btn btn-primary" data-toggle="modal"
          data-target="#exampleModalCenter"></button>
      </div>
      <Modal/>
    </>
  );
};

export default Qnas;
