import React, {useState} from "react";
import axios from "axios";
class Qna extends React.Component {
  state = {
    aContent: []
  }

  handleDelete(id) {
    axios
      .delete("http://localhost:8080/api/QnAdelete/" + id)
      .then(res => {
        alert("QnA가 삭제 완료");
        window.location.reload();
      })
      .catch(err => {
        console.log(err);
      });
  }

  handleCreate(qid, id, content) {
    axios
      .put("http://localhost:8080/api/AUpdate", {
        aregdate: "null",
        awriter: this.props.lm.id,
        content: this.state.aContent,
        qnaid: qid,
        qregdate: "null",
        qwriter: "ssafy",
        title: ""
      })
      .then(res => {
        alert("QnA가 등록 완료");
        window.location.reload();
      })
      .catch(err => {
        console.log(err);
      });
  }
  
  handleContentChange= e =>{
    this.setState({aContent : e.target.value})
  }

  render() {
    const { key, accodionKey, qna, lm } = this.props;
    const accKey = "#collapseTwo" + accodionKey;
    return (
      <>
        <div className="panel panel-default" >
          <div className="panel-heading" role="tab" id="headingTwo">
            <h4 className="panel-title">
              <a
                className="collapsed"
                role="button"
                data-toggle="collapse"
                data-parent="#accordion"
                href={accKey}
                aria-expanded="false"
                aria-controls="collapseTwo"
              >
                Q. {qna.title}
              </a>
            </h4>
          </div>
          <div
            id={accKey.slice(1, accKey.length)}
            className="panel-collapse collapse"
            role="tabpanel"
            aria-labelledby="headingTwo"
          >
            <div className="panel-body">
              <div class="input-group-prepend">
                <span class="input-group-text"></span>
              </div>
              {lm.auth === 1 ? (
                <textarea class="form-control" aria-label="With textarea" onChange={this.handleContentChange}>
                  {qna.content}
                </textarea>
              ) : (
                qna.content==="null" ? "답변이 없습니다." : qna.content
              )}
              <div align="right">
                {lm.auth === 1 ? (
                  <div
                    className="btn btn-primary"
                    id={accodionKey}
                    onClick={() => {
                      this.handleCreate(qna.qnaid);
                    }}
                  >
                    {" "}
                    등록
                  </div>
                ) : (
                  ""
                )}
                {lm.auth === 1 ? (
                  <div
                    className="btn btn-danger"
                    id={accodionKey}
                    onClick={() => {
                      this.handleDelete(qna.qnaid);
                    }}
                  >
                    삭제
                  </div>
                ) : (
                  ""
                )}
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default Qna;
