import React from "react";
import axios from "axios";

class Modal extends React.Component {
  state = {
    qcontent: "",
    loginMember : {}
  };
  componentDidMount(){
    const getLoginCheck = () => {
      return new Promise((resolve, reject) => {
        axios
          .get("http://localhost:8080/api/loginid")
          .then(res => {
            this.setState({...this.state, loginMember : res.data});
            resolve();
          })
          .catch(err => {
            reject();
            // window.location = 'http://localhost:8080/';
          });
      });
    };
    getLoginCheck();
  }
  handleChange = e => {
    this.setState({
      ...this.state,
      qcontent: e.target.value
    });
  };
  handleSubmit = () => {
    const getLoginCheck = () => {
      return new Promise((resolve, reject) => {
        axios
          .get("http://localhost:8080/api/loginid")
          .then(res => {
            resolve();
          })
          .catch(err => {
            alert("로그인 해주세요");
            reject();
            // window.location = 'http://localhost:8080/';
          });
      });
    };
    const postQnaRegist = () => {
      return new Promise(resolve => {
        axios
          .post("http://localhost:8080/api/QnAregist", {
            aregdate: "null",
            awriter: "null",
            content: "null",
            qnaid: 0,
            qregdate: "null",
            qwriter: "ssafy",
            title: this.state.qcontent
          })
          .then(res => {
            resolve();
          })
          .catch(err => {
            console.log(err);
          });
      });
    };
    getLoginCheck().then(postQnaRegist).then(window.location.reload());
  };
  render() {
    return (
      <>
        <div
          className="modal fade"
          id="exampleModalCenter"
          tabindex="-1"
          role="dialog"
          aria-labelledby="exampleModalCenterTitle"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-dialog-centered" role="document">
            <div className="modal-content">
              <div className="modal-header" align="center">
                <h3 className="modal-title" id="exampleModalCenterTitle">
                  QnA등록
                </h3>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-label="Close"
                >
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div className="modal-body">
                <form>
                  <div className="form-group">
                    <label for="exampleInputEmail1">질문</label>
                    <input
                      type="text"
                      onChange={this.handleChange}
                      className="form-control"
                      id="exampleInputEmail1"
                      aria-describedby="emailHelp"
                      placeholder="질문 내용 입력..."
                    />
                  </div>
                </form>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-secondary"
                  data-dismiss="modal"
                >
                  Close
                </button>
                <button
                  type="button"
                  className="btn btn-primary"
                  onClick={this.handleSubmit}
                  data-dismiss="modal"
                >
                  등록
                </button>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default Modal;
